<template>
  <div class>
    <v-app-bar

      color="primary"
      dark
      style="margin-bottom:20px"

    >
      <div class="">
        <span class="headline">
        AYAME</span>
      </div>

      <v-spacer></v-spacer>

      <div class="">
        <logout />
      </div>

    </v-app-bar>

        <v-row dense>
          <v-col :cols="1"></v-col>
          <v-col v-for="course in courses" :key="course.name" :cols="10">
            <v-card>
              <v-card-title v-text="course.name"></v-card-title>
              <v-card-text>
                <v-col v-for="chapter in course.chapters" :key="chapter.id" :cols="12">
                  <v-card>
                    <v-card-title v-text="chapter.name"></v-card-title>
                    <v-card-text>
                      <v-list>
                        <v-list-item-group color="primary">
                          <v-list-item v-for="qc in chapter.question_categories" :key="qc.id">
                            <v-list-item-content>
                              <div v-if="qc.questions.length === 0">
                                {{qc.name}} <div> 0 /0 </div>
                              </div>
                              <div v-else @click="goToQuestion(qc, chapter, course)">
                                {{qc.name}} <div> {{countResponses(qc) }} /{{qc.questions.length}}</div>
                              </div>
                            </v-list-item-content>
                          </v-list-item>
                        </v-list-item-group>
                      </v-list>
                    </v-card-text>
                  </v-card>
                </v-col>
              </v-card-text>

            </v-card>
          </v-col>

        </v-row>
  </div>
</template>

<script>
import logout from '@/components/Logout.vue'
export default {
  name: 'Courses',

  components: { logout },

  methods: {
    fetchAll: function (errFun) {
      this.fetchCourses(errFun)
      this.fetchResponses(errFun)
    },

    countResponses: function (qc) {
      if (qc.questions !== null) {
        const qAllIds = new Set(qc.questions.map(q => q.id))
        const qRespondedIdes = new Set(this.responses.map(res => res.question_id))
        const intersect = new Set(
          [...qAllIds].filter(x => qRespondedIdes.has(x))
        )

        return intersect.size
      } else {
        return 0
      }
    },

    goToQuestion: function (qc, chapter, course) {
      const ids = qc.questions.map(x => x.id)
      let qid = 1
      if (ids.includes(this.$store.currentQuestionId)) {
        qid = this.$store.currentQuestionId
      } else {
        qid = ids[0]
      }
      console.log(qc, chapter, course)
      this.$store.commit('setAllCurrentIds', { courseId: course.id, chapterId: chapter.id, qcId: qc.id, questionId: qid })
      this.$router.push({ name: 'Question', params: { id: qid } })
    }
  },

  mounted: function () {
    const token = this.$store.state.token
    if (token === null) {
      console.log('no token')
      this.$router.push({ name: 'Login' })
      return false
    }
    this.fetchAll(() => this.$router.push({ name: 'Login' }))
    this.courses = this.$store.state.courses
    this.responses = this.$store.state.responses
    // const cc = this.$store.state.courses.find(course => course.id === this.$store.state.currentCourseId)
    // console.log(cc)
  },

  data: () => ({
    courses: {},
    responses: {}

    //
  })
}
</script>
